

const Note = props => {
    return (
        <div onClick={(event) => props.noteObject.isCompleted ? event.target.className = "done" : props.noteObject.isCompleted = false} 
        className={props.noteObject.isCompleted ? "done" : null} >
            {props.noteObject.note}
            <br/>
            {props.noteObject.isCompleted}
        </div>
    );
}

export default Note
